  box-shadow: 0 0 20px var(--night-glow), 0 0 40px var(--night-glow);
}
.dark .glow-text {
  text-shadow: 0 0 10px var(--night-glow), 0 0 20px var(--night-glow);
}
.dark .bio-glow {
  color: var(--night-accent);
  text-shadow: 0 0 5px var(--night-accent), 0 0 10px var(--night-accent);
}
.touch-target {
  min-height: 44px;
  min-width: 44px;
}
@media (max-width: 640px) {
  .timer-display {
    font-size: 4rem;
  }
}
@media (min-width: 641px) and (max-width: 768px) {
  .timer-display {
    font-size: 6rem;
  }
}
@media (min-width: 769px) {
  .timer-display {
    font-size: 8rem;
  }
}
.loading-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
.confetti-container {
  position: fixed;
  inset: 0;
  pointer-events: none;
  z-index: 9999;
}
@media (prefers-reduced-motion: reduce) {
  *,
